header {
  height: 70px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}
header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
header nav li {
  padding: 0 26px;
}
header nav li:last-child {
  padding-right: 0;
}
header nav li a {
  color: #A5A5A5;
  font-weight: 700;
  font-size: 1.1rem;
  text-transform: uppercase;
  padding: 28px 18px 22px;
  border-bottom: 4px solid transparent;
  letter-spacing: 0.1875rem;
}
header nav li a:hover {
  color: #000000;
  border-color: #08A6E4;
}
header ul {
  display: flex;
}
@media (max-width: 792px) {
  header ul {
    position: absolute;
    left: 0;
    top: 95px;
    flex-direction: column;
    background-color: white;
    width: 100%;
    padding-bottom: 20px;
    display: none;
  }
  header ul li {
    padding: 0;
  }
  header ul a {
    display: block;
    padding: 14px 20px;
  }
}
header .menu__btn {
  display: none;
  position: absolute;
  top: -9999px;
  left: -9999px;
  opacity: 0;
}
@media (max-width: 792px) {
  header .menu__btn:checked ~ ul {
    display: flex;
  }
  header .menu__btn:checked ~ .menu__toggle span:nth-child(1) {
    transform: rotate(45deg) translate(2px, 5px);
  }
  header .menu__btn:checked ~ .menu__toggle span:nth-child(2) {
    opacity: 0;
  }
  header .menu__btn:checked ~ .menu__toggle span:nth-child(3) {
    transform: rotate(-45deg) translate(2px, -5px);
  }
}
header .menu__toggle {
  display: none;
}
header .menu__toggle span {
  width: 20px;
  height: 2px;
  display: block;
  margin-bottom: 3px;
  background-color: #08A6E4;
  transition: 0.3s all ease-in-out;
}
header .menu__toggle span:last-child {
  margin-bottom: 0;
}
@media (max-width: 792px) {
  header .menu__toggle {
    display: block;
  }
}

